import {
  beforeEach,
  beforeEachProviders,
  describe,
  expect,
  it,
  inject,
} from '@angular/core/testing';
import { ComponentFixture, TestComponentBuilder } from '@angular/compiler/testing';
import { Component } from '@angular/core';
import { By } from '@angular/platform-browser';
import { ActivitiesMessageComponent } from './activities-message.component';

describe('Component: ActivitiesMessage', () => {
  let builder: TestComponentBuilder;

  beforeEachProviders(() => [ActivitiesMessageComponent]);
  beforeEach(inject([TestComponentBuilder], function (tcb: TestComponentBuilder) {
    builder = tcb;
  }));

  it('should inject the component', inject([ActivitiesMessageComponent],
      (component: ActivitiesMessageComponent) => {
    expect(component).toBeTruthy();
  }));

  it('should create the component', inject([], () => {
    return builder.createAsync(ActivitiesMessageComponentTestController)
      .then((fixture: ComponentFixture<any>) => {
        let query = fixture.debugElement.query(By.directive(ActivitiesMessageComponent));
        expect(query).toBeTruthy();
        expect(query.componentInstance).toBeTruthy();
      });
  }));
});

@Component({
  selector: 'test',
  template: `
    <sa-activities-message></sa-activities-message>
  `,
  directives: [ActivitiesMessageComponent]
})
class ActivitiesMessageComponentTestController {
}

